img{
    width:100%;
}

.product{
    border:2px solid #ccc;
    border-radius:5px;
}

.product-info{
    text-align:center;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}

p{
    font-size:1.2em;
}

.add-remove{
    display:flex;
    justify-content:center;
    align-items:center;
    padding-block:0.5em;
    cursor:pointer;  
}

.button{
    margin-inline:1em;
    height:30px;
    width:30px;
    font-size:1.5em;
     
}