.card{
    display:grid;
    grid-template-columns:1fr 2fr 1fr;
    gap:20px;
    margin-block:0.5rem;
    padding-inline:1rem;
}

img{
    width:100%;
}

.product-number{
    display:flex;
    justify-content:center;
    align-items:center;
    gap:1rem;
}

.product-number p{
    border:2px solid #ccc;
    height:2em;
    width:2em;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
}

.productInfo{
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    gap:1.5rem;
}

.productInfo p{
    font-size:1.2em;
}

.description{
    font-size:1em;
    color:#c0c0c0;
}

