.navbar-container{
    background-color:rgb(170, 230, 230);
    display:flex;
    justify-content:space-between;
    padding:0.5em 2rem;
    box-shadow:0 2px 2px #ccc;
    position:sticky;
    top:0;
}
.navbar-container h1{
    color:teal;
    cursor:pointer;
}

.cart-info{
    display:flex;
    justify-content:space-between;
    align-items:center;
}

.cart-item{
    margin-inline:1rem;
    font-size:2rem;
    color:teal;
    cursor:pointer;
}