.cart-info{
    margin-block:0.5rem;
}

.cartnav{
    padding-inline:1rem;
    position:sticky;
    top:0;
    background-color:rgb(170, 230, 230);
    padding-block:0.5em;
    color:teal;
}

.total{
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin-inline:1rem;
    margin-bottom:2rem;
}

th,td{
    padding:0.3em;
    text-align:center;
}

.bill button{
    padding:0.6em;    
    background-color:rgb(52, 221, 114);
    border:2px solid rgb(52, 221, 114);
    border-radius:25px;
    color:#fff;
    margin-left:2em;
    margin-block:1em;
    cursor:pointer;
}

.promo-code-container{
    width:30rem;
    height:2rem;
    border:0 solid rgb(52, 221, 114);
    margin-block:1rem;
    display:flex;
    align-items:center;
    padding-left:0;
    border-radius:10px;
    background-color:rgb(52, 221, 114);
}

.promo-code-container input{
    width:80%;
    height:100%;
    border-radius:10px;
    border-color:rgb(52, 221, 114);
    outline:none;
    padding:0.5em;
}

.promo-code-container span{
    
    margin-inline:auto;
    color:#fff;
    font-size:1.5rem;
}

